ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
  }
  
  a {
    text-decoration: none;
  }
  
  .nav {
    display: flex;
    justify-content: space-between;
    height: 5vh;
  }
  
  .nav__item {
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0 auto;
    text-align: center;
    background-color: cornflowerblue;
    border: 1px solid white;
  }
  
  .nav__item--link {
    display: block;
    width: 100%;
    height: 100%;
    line-height: 5vh;
    color: white;
  }
  